<script>
// import Child from "@/components/auth/show-survey-result/show-survey-result.vue";
export default {
  name: 'take-survey',
  data () {
    return {
      token: '',
      risklevel: '',
      sum: 0,
      questionArray: [],
      question1: '',
      question2: '',
      question3: '',
      question4: '',
      question5: '',
      question6: '',
      question7: '',
      question8: '',
      question9: '',
      question1Errors: [],
      question2Errors: [],
      question3Errors: [],
      question4Errors: [],
      question5Errors: [],
      question6Errors: [],
      question7Errors: [],
      question8Errors: [],
      question9Errors: [],
    }
  },
  computed: {
    created: function () {
      this.questionArray.push(this.question1)
      this.questionArray.push(this.question2)
      this.questionArray.push(this.question3)
      this.questionArray.push(this.question4)
      this.questionArray.push(this.question5)
      this.questionArray.push(this.question6)
      this.questionArray.push(this.question7)
      this.questionArray.push(this.question8)
      this.questionArray.push(this.question9)

      return this.questionArray
    },
    formReady () {
      return (
        !this.question9Errors.length &&
        !this.question8Errors.length &&
        !this.question7Errors.length &&
        !this.question6Errors.length &&
        !this.question5Errors.length &&
        !this.question4Errors.length &&
        !this.question3Errors.length &&
        !this.question2Errors.length &&
        !this.question1Errors.length
      )
    },
    // Risk Level - Analysis here
    calrisk: function () {
      this.sum = parseInt(this.question1) +
                 parseInt(this.question2) +
                 parseInt(this.question3) +
                 parseInt(this.question4) +
                 parseInt(this.question5) +
                 parseInt(this.question6) +
                 parseInt(this.question7) +
                 parseInt(this.question8) +
                 parseInt(this.question9)
      console.log(this.sum)
      return this.sum
    },
  },
  methods: {
    onsubmit () {
      this.question1Errors = this.question1 ? [] : ['Question1 is required']
      this.question2Errors = this.question2 ? [] : ['Question2 is required']
      this.question3Errors = this.question3 ? [] : ['Question3 is required']
      this.question4Errors = this.question4 ? [] : ['Question4 is required']
      this.question5Errors = this.question5 ? [] : ['Question5 is required']
      this.question6Errors = this.question6 ? [] : ['Question6 is required']
      this.question7Errors = this.question7 ? [] : ['Question7 is required']
      this.question8Errors = this.question8 ? [] : ['Question8 is required']
      this.question9Errors = this.question9 ? [] : ['Question9 is required']
      if (!this.formReady) {
        return
      }
      // const token = this.$store.state.app.user.token
      let risklevel = ''
      if (this.calrisk < 13) {
        risklevel = 'LEVEL - LOW RISK'
      } else if (this.calrisk >= 13 && this.calrisk < 19) {
        risklevel = 'LEVEL - MEDIUM RISK'
      } else if (this.calrisk >= 18) {
        risklevel = 'LEVEL - HIGH RISK'
      }

      let questionArray = []
      if (this.created != null) {
        questionArray = this.questionArray
      }

      this.$store
        .dispatch('takesurveyapi', {
          // token,
          risklevel,
          questionArray,
        })
        .then(() => {
          this.$router.push({
            name: 'dashboard',
          })
        })
        .catch(err => {
          console.log(err)
          this.snackbar = true
        })
    },
  },
}
</script>

